<template>
    <div>
        <div class="content">
            <div v-for="(entrance, index) in entrances" :key="index" class="entrance-frame">
                <div class="entrance">
                    <div class="title" @click="jump(entrance.id)">{{ entrance.title }}</div>
                    <div class="post-date">{{ entrance.date }}</div>
                </div>
                <div class="introduce">{{ entrance.introduce }}</div>
            </div>
        </div>
        <el-pagination layout="prev, pager, next" :total="archivesnum" :page-size="5" ></el-pagination>
    </div>
</template>

<script>
export default {
    name: 'Home',
    data() {
        return {
            archivesnum: 5,
            entrances: [
                {id: '1', title: 'Hello, World!', date: '2020.2.17', introduce: 'this is a test theme'},
                {id: '2', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
                {id: '3', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
                {id: '4', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
                {id: '5', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'}
            ]
        }
    },
    methods: {
        jump(id) {
            this.$router.push({path: '/archives/' + id});
        }
    },
    created() {
        this.$emit('tooltip', 'Welcome back');
    }
}
</script>

<style scoped>
    .content {
        margin: 0 auto;
        max-width: 450px;
        text-align: left;
    }

    .entrance-frame {
        margin-bottom: 30px;
    }
    .entrance-frame .entrance {
        margin-bottom: 5px;
        display: flex;
        justify-content: space-between;
    }
    .entrance-frame .entrance .title {
        position: relative;
        font-size: 18px;
        font-weight: 600;
        border-bottom: 2px solid rgba(0, 0, 0, 0);
        cursor: pointer;
    }
    .entrance-frame .entrance .title:hover {
        border-bottom: 2px solid;
    }
    .entrance-frame .entrance .post-date {
        font-size: 13px;
        color: #757575;
    }
    .entrance-frame .introduce {
        font-size: 13px;
        color: #757575;
    }
</style>


